import React, { Component } from 'react';
import {Card,Button,CardDeck,CardColumns} from 'react-bootstrap';

import ProjectStore from '../../store/projectStore';
export default class maintenance extends Component  {
   constructor(props){
        super(props);
        this.state = {
            Months:['JAN','FEB','MAR','APR','JUN','JUL','AUG','SEP','OCT','NOV','DEC'],
            categories:[{name:'monthly'},{name:'Bore fund'},{name:'Watchman Room fund'}]
        }      
    }
    
    render(){
        return(	           
            <div style={{"padding-top":"35px"}}>
            <Card>
              <Card.Header>JAN</Card.Header>
              <Card.Body>
            <CardColumns>
             <Card>             
              <Card.Body>
                <Card.Title>Monthly Maintenance</Card.Title>
                <Card.Text>
                  PAID - &#8377; 1000
                </Card.Text>
              
              </Card.Body>
            </Card>
                <Card>             
              <Card.Body>
                <Card.Title>Bore Well Fund</Card.Title>
                <Card.Text>
                  PAID - &#8377; 1000
                </Card.Text>
              
              </Card.Body>
            </Card>            
            </CardColumns>
              </Card.Body>
            </Card>
            <hr/>
              <Card>
              <Card.Header>FEB</Card.Header>
              <Card.Body>
            <CardColumns>
             <Card>             
              <Card.Body>
                <Card.Title>Monthly Maintenance</Card.Title>
                <Card.Text>
                  PAID - &#8377; 1000
                </Card.Text>
              
              </Card.Body>
            </Card>
                <Card>             
              <Card.Body>
                <Card.Title>Borewell Fund</Card.Title>
                <Card.Text>
                  PAID - &#8377; 1000
                </Card.Text>
              
              </Card.Body>
            </Card>
            
            </CardColumns>
              </Card.Body>
            </Card>
            </div>
        )
    }

}


