import React, { Component } from 'react';
import { EventEmitter } from 'fbemitter';
var self;
var emitter = new EventEmitter();
export default class ProjectStore extends EventEmitter {
    static lodingCount = 0;
    static authDetails = {};
    static isAuthenticated = false;
  
    static subscribe(eventType, callback) {
        console.log("Calledd EVENT LITERS.....");
        var listener = emitter.addListener(eventType, callback);
        return listener;
    }

    static unsubscribe(eventType, callback) {
        emitter.removeAllListeners(eventType);
    }
    static dispatch(eventType) {
        emitter.emit(eventType);
    }
   
    static onInit() {
        ProjectStore.lodingCount = 0;
    }

    static setLodingCount(countValue) {
        ProjectStore.lodingCount = countValue;
    }

    static getLodingCount() {
        return ProjectStore.lodingCount;
    }
    
    
    static setAuthentication(object){
        ProjectStore.authDetails = object;
        ProjectStore.isAuthenticated = true;
        emitter.emit("onAuthentication");
    }
    static logoffUser(){
        ProjectStore.authDetails = {};
        ProjectStore.isAuthenticated = false;    
          emitter.emit("onLogoff");
        
    }

}



