import React, { Component } from 'react';
import {Card,Button,CardDeck,Badge,Alert,CardColumns} from 'react-bootstrap';

import ProjectStore from '../../store/projectStore';
export default class expenses extends Component  {
   constructor(props){
        super(props);
        this.state = {
            Months:['JAN','FEB','MAR','APR','JUN','JUL','AUG','SEP','OCT','NOV','DEC'],
            categories:[{name:'monthly'},{name:'Bore fund'},{name:'Watchman Room fund'}]
        }      
    }
    
    render(){
        return(	           
            <div style={{"padding-top":"35px"}}>
            <Card>
              <Card.Header>JAN</Card.Header>
              <Card.Body>
              <CardColumns>
                <Card> 
                    <Card.Body>
                     <Card.Title>Power Bill</Card.Title>                             
                    <Alert variant="primary">Billed: </Alert>
                    <Alert variant="success">Paid: </Alert>
                    <Alert variant="warning">Balance: </Alert>  
                    </Card.Body>
                </Card>
                <Card> 
                    <Card.Body>
                     <Card.Title>Watch Man</Card.Title>                             
                    <Alert variant="primary">Billed: </Alert>
                    <Alert variant="success">Paid: </Alert>
                    <Alert variant="warning">Balance: </Alert>  
                    </Card.Body>
                </Card>
                <Card> 
                    <Card.Body>
                     <Card.Title>Borewell Fund</Card.Title>                             
                    <Alert variant="primary">Billed: </Alert>
                    <Alert variant="success">Paid: </Alert>
                    <Alert variant="warning">Balance: </Alert>  
                    </Card.Body>
                </Card>
                
              
             </CardColumns>
            </Card.Body>
            </Card>
            </div>
        )
    }

}


