import React, { Component } from 'react';
import { Router, Route, hashHistory } from 'react-router';
import ProjectStore from '../store/projectStore';
import authentication from '../contents/auth/authentication';
import maintenance from '../contents/pages/maintenance';
import expenses from '../contents/pages/expenses';
export default class ContentLoader extends Component  {
    constructor(props){
        super(props);       
         this.onSuccessfulAuthentication = this.onSuccessfulAuthentication.bind(this);
        this.onSuccessfullLogoff =this.onSuccessfullLogoff.bind(this);
         ProjectStore.subscribe("onAuthentication",this.onSuccessfulAuthentication);
         ProjectStore.subscribe("onLogoff",this.onSuccessfullLogoff);
         if(ProjectStore.isAuthenticated){
                 hashHistory.push("/maintenance");
         }else{
             this.onSuccessfullLogoff();
         }
    }
     onSuccessfulAuthentication(){
              hashHistory.push("/maintenance");
     }
    onSuccessfullLogoff(){
        hashHistory.push("/");
    }

    render(){
        return(	           

        <div className="container-fluid">    
             <Router history={hashHistory}>
                 <div>
                        <Route path="/" component={authentication} />     
                        <Route path="/maintenance" component={maintenance} />    
                        <Route path="/expenses" component={expenses} />      
                   </div>
            </Router>
        </div>
        )
    }

}


