import React, { Component } from 'react';
import ProjectStore from '../../store/projectStore';
import nmlogo from '../../nmloo.png';
export default class authentication extends Component  {
    constructor(props){
        super(props);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
    }
    
    handleOnSubmit(){
  
        ProjectStore.setAuthentication({"owner":"hell"});
    }

    render(){
        return(	           

           
         <div className="container h-100">
  <div className="row h-100 justify-content-center align-items-center">
    <form className="col-12 col-md-5" >
         <div className="card card-signin my-4">
             <div class="card-header text-center"><img src={nmlogo} style={{"height":"75px"}} /><h3>NANDAN MEADOWS</h3></div>
          <div className="card-body">           
      <div className="form-group">
        <label for="formGroupExampleInput">Plot Number </label>
        <input type="text" className="form-control" id="formGroupExampleInput" placeholder="Plot Number" />
            <small>(only numbers- For parts please add (.) with plot nuber like 66.1 )</small>
      </div>
      <div className="form-group">
        <label for="formGroupExampleInput2">Owner Name</label>
        <input type="text" className="form-control" id="formGroupExampleInput2" placeholder="Owner Name" />
      </div>  
       <div className="form-group">
        <label for="formGroupExampleInput2">Mobile Number</label>
        <input type="text" className="form-control" id="formGroupExampleInput2" placeholder="Mobile Number" />
        <small>(For india - don't include +91 )</small>
      </div>
              <div className="form-group">
       
        <button type="button" className="btn btn-primary" onClick={this.handleOnSubmit}>
                Put Forward
        </button>
       
      </div>
          </div>
        </div>
    </form>   
  </div>  
</div>
        
        )
    }

}


