import React from 'react';
import {Navbar,Nav,NavDropdown} from 'react-bootstrap';
import ProjectStore from '../store/projectStore';
import nmlogo from '../nmloo.png';




class Navigation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isToggleOn: false,
      showNavigation:false
    };
    this.onSuccessfulAuthentication = this.onSuccessfulAuthentication.bind(this);
      this.logoffUser = this.logoffUser.bind(this);
      ProjectStore.subscribe("onAuthentication",this.onSuccessfulAuthentication)
  }
 onSuccessfulAuthentication(){
     
      this.setState({
          showNavigation:true
      });
     console.log(this.props);
  }
 logoffUser(){
     ProjectStore.logoffUser();
      this.setState({
          showNavigation:false
      });
 }
  render() {
      if(this.state.showNavigation) {
    return (
     <Navbar bg="light" expand="lg" >
  <Navbar.Brand href="#home"><img src={nmlogo} style={{"height":"35px"}}/></Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="mr-auto">
      <Nav.Link href="#maintenance">Maintenance</Nav.Link>
      <Nav.Link href="#expenses">Expenses</Nav.Link>
     {/* <NavDropdown title="Dropdown" id="basic-nav-dropdown">
        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
      </NavDropdown>*/}
    </Nav>    
     <Nav>
        <Navbar.Text>
        Signed in as: Owner Name
        </Navbar.Text>
      <Nav.Link href="javascript:void(0)" onClick={this.logoffUser}>Logout</Nav.Link>      
    </Nav>
  </Navbar.Collapse>
</Navbar>
    )
    }
      else{
          return (<div className="hidden"></div>)
      }
  }
}

export default Navigation;


